.App {
  text-align: center;
}

.About {
  background-color: white;
  margin-top: 90px;
  margin-right: 7px;
  margin-left: 28px;
}

.border {
  background-color: rgb(245, 243, 243);
}

row {
  display: flex;
  flex-wrap: wrap;
}

.aboutmain {
  width: 50em;
  height: 7em;
  border-radius: 7px;
}

.aboutProj {
  background-color: rgb(245, 243, 243);
  height: 3rem;
  background-size: cover;
  background-position: center;
}

@media screen and (max-width: 770px) {
  .aboutmain {
    width: 30em;
    text-align: left;
  }
  .About {
    margin-top: 120px;
  }
}

@media screen and (max-width: 470px) {
  .aboutmain {
    width: 25em;
    text-align: left;
  }
  .aboutProj {
    height: 4rem;
  }
  .About {
    margin-top: 120px;
  }
}
.grid-container {
  display: grid;
  row-gap: 10px;
  column-gap: 10px;
  height: 500px;
  padding: 20px 10px 0px 10px;
  width: 80%;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  align-items: center;
  justify-content: center;
  grid-template-rows: 1fr 1fr 1fr;
}

#image3 {
  grid-column-start: 2;
  grid-column-end: 3;
}
#image1 {
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 2;
}

#image4 {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 3;
  grid-column-end: 5;
}
#image5 {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 3;
}
#image6 {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 3;
  grid-column-end: 4;
}
#image7 {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 3;
  grid-column-end: 4;
}
#image8 {
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 4;
  grid-column-end: 5;
}
#image9 {
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 5;
  grid-column-end: 6;
}
#image10 {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 5;
  grid-column-end: 6;
}
.image {
  padding: 0 0px 10px 2px;
  width: 100%;
  height: 10%;
}
.output {
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
  height: 70%;
}
.display-data {
  height: 60%;
  overflow-y: hidden;
}
.resultContainer {
  overflow: hidden;
}
.content {
  justify-content: left;
  margin-top: -290px;
  background-color: white;
  width: 60%;
  height: 30%;
  padding: 20px;
  overflow-y: hidden;
}
h1 {
  color: cadetblue;
}

.back {
  display: inline-block;
  padding: 10px 30px;
  color: cadetblue;
  background-color: transparent;
  border: 2px solid cadetblue;
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  margin-top: 30px;
  transition: 0.3s ease background-color;
  transition-property: background-color, color;
}
.back:hover {
  color: white;
  text-decoration: none;
  font-weight: bold;
  background-color: cadetblue;
}
.MuiListSubheader-gutters {
  display: none;
}
#header {
  position: absolute;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100vw;
  text-decoration: none;

  height: 80px;

  border-bottom: #fff;

  background: rgba(0, 0, 0, 0.6);
}
nav .logo {
  padding: 12px 20px;
  height: 80px;
  float: left;
  font-size: 30px;
  font-weight: bold;
  text-transform: uppercase;
  color: cadetblue;
  text-decoration: none;
}
nav ul {
  list-style: none;
  float: right;
  margin: 0;
  padding: 0;
  display: flex;
  text-decoration: none;
}

nav ul li a {
  line-height: 80px;
  color: cadetblue;
  padding: 12px 25px;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  list-style: none;
}
nav ul li a:hover {
  background: cadetblue;
  color: white;
  border-radius: 6px;
  text-decoration: none;
  list-style: none;
}
nav ul li a:active {
  text-decoration: none;
}
.image-list {
  margin: 20px;
  width: 100vw;

  padding-right: 0px;
  margin-right: 0;
  margin-top: 60px;
  margin-left: 8px;
}
.grid-container {
  padding-top: 78px;
  padding-left: 40px;
  padding-right: 40px;
  margin-right: 40px;
  margin-top: 60px;
  margin-left: 15%;
  width: 75%;
  overflow-x: hidden;
  overflow-y: hidden;
}
.about-background {
  color: cadetblue;
  font-size: 24px;
}
#header .rightSide button {
  display: none;
}

#header .rightSide #hidden {
  display: flex;
}

@media (max-width: 700px) {
  #header .rightSide button {
    display: flex;
    float: right;
    margin: 20px;
    margin-top: 24px;
    margin-right: 10px;
  }

  #header .rightSide .navbar-links {
    display: none;
  }

  #header .rightSide #hidden {
    position: absolute;
    left: 0px;
    top: 80px;
    height: 230px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 190px;
  }

  #header .rightSide #hidden a {
    margin: -20px;
  }

  .content {
    justify-content: left;
    margin-top: -230px;
    background-color: white;
    width: 60%;
    height: 30%;
    padding: 20px;
  }
  button {
    background-color: rgba(0, 0, 0, 0.6);
    border: none;
    color: cadetblue;
  }
}
