.grid-container {
  display: grid;
  grid-row-gap: 10px;
  row-gap: 10px;
  grid-column-gap: 10px;
  column-gap: 10px;
  height: 500px;
  padding: 20px 10px 0px 10px;
  width: 80%;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  align-items: center;
  justify-content: center;
  grid-template-rows: 1fr 1fr 1fr;
}

#image3 {
  grid-column-start: 2;
  grid-column-end: 3;
}
#image1 {
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 2;
}

#image4 {
