:root {
  --hover-width: 99%;
  --other-width: 1%;
  --font-family: "Work Sans", sans-serif;
}

.main {
  padding: 0;
  margin: 0;
  font-family: "Work Sans", sans-serif;
  font-family: var(--font-family);
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  text-decoration: none;
  box-sizing: border-box;
  background-color: black; 
}

h2 {
  text-transform: uppercase;
  font-size: 8rem;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.9);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  white-space: nowrap;
  z-index: 1;
}

h2 strong {
  font-weight: 200;
}

.container {
  background: url(/Frontend-final-project/static/media/imageBW.41480ff4.jpg) no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  position: relative;
  height: 100vh;
}

.split {
  position: absolute;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.split.left:before {
  content: "";
  position: absolute;
  background: url(/Frontend-final-project/static/media/image.f429b5ef.jpg) no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.split.right {
  right: 0;
}

.split.left,
.split.right {
  transition: 1000ms all ease-in-out;
}

.split.left *,
.split.right * {
  opacity: 0;
  transition: 1000ms all ease-out;
}

.hover-left .left {
  width: 99%;
  width: var(--hover-width);
}

.hover-left .left * {
  opacity: 1;
}
.hover-left .right {
  width: 1%;
  width: var(--other-width);
}

.hover-right .right {
  width: 99%;
  width: var(--hover-width);
}

.hover-right .right * {
  opacity: 1;
}

.hover-right .left {
  width: 1%;
  width: var(--other-width);
}

@media (max-width: 800px) {
  h1 {
    font-size: 2rem;
  }
}

